import { createRouter, createWebHistory } from "vue-router";
// import NewsView from "../views/NewsView.vue";
// import JobsView from "../views/JobsView.vue";
// import AskView from "../views/AskView.vue";
import UserView from "../views/UserView.vue";
import ItemView from "../views/ItemView.vue";
import createListView from "../views/CreateListView.js";

// Vue.use(VueRouter);
// this.$routeÎ°ú Í∞Å ÌéòÏù¥ÏßÄÏùò ÎùºÏö∞Ìä∏Ï†ïÎ≥¥Ïóê Ï†ëÍ∑ºÌï† Ïàò ÏûàÎã§.
const routes = [
  {
    path: "/",
    redirect: "news",
  },
  {
    //path: url Ï£ºÏÜå
    path: "/news",
    name: "news",
    //component: url Ï£ºÏÜåÎ°ú Í∞îÏùÑ Îïå ÌëúÏãúÎê† Ïª¥Ìè¨ÎÑåÌä∏ =page
    // component: NewsView,
    component: createListView("NewsView"),
  },
  {
    path: "/ask",
    name: "ask",
    // component: AskView,
    component: createListView("AskView"),
  },
  {
    path: "/jobs",
    name: "jobs",
    // component: JobsView,
    component: createListView("JobsView"),
  },
  {
    //paramsÏúºÎ°ú vue ÎùºÏö∞ÌÑ∞Í∞Ä ÏßëÏñ¥ÎÑ£ÎäîÎã§.
    path: "/user/:id",
    component: UserView,
  },
  {
    path: "/item/:id",
    component: ItemView,
  },
];

// ÎùºÏö∞ÌÑ∞ ÏÉùÏÑ±
const router = createRouter({
  history: createWebHistory(),
  routes,
});

// ÎùºÏö∞ÌÑ∞ Ï∂îÏ∂ú (main.jsÏóêÏÑú import)
export { router };
