// node_modules/axios ÎÖ∏ÎìúÎ™®ÎìàÏóê ÏÑ§ÏπòÎêú ÎùºÏù¥Î∏åÎü¨Î¶¨ axiosÎ•º Îì§Í≥†Ïò®Îã§.
import axios from "axios";

// 1. HTTP Request & ResponseÏôÄ Í¥ÄÎ†®Îêú Í∏∞Î≥∏ ÏÑ§Ï†ï (ÌÜ†ÌÅ∞Ï≤òÎ¶¨ÌïúÎã§Í≥† ÌïòÎ©¥ ÌÜ†ÌÅ∞Í¥ÄÎ†®Îêú Î°úÏßÅÎì§ÎèÑ Í≥µÌÜµÏ†ÅÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏùå)
//Ï£ºÏÜåÍ∞Ä Îí∑ Î∂ÄÎ∂ÑÏù¥ Îã¨ÎùºÏßÄÍ≥† ÏïûÎ∂ÄÎ∂ÑÏùÄ ÎèôÏùºÌï† Îïå, baseUrlÎ°ú Î¨∂Ïñ¥ÏÑú.
const config = {
  baseUrl: "https://api.hnpwa.com/v0/",
};

// 2. API Ìï®ÏàòÎì§ÏùÑ Ï†ïÎ¶¨
//baseUrl Îã§Ïùå +Î°ú Î≥ÄÏàòÎ•º Ïó∞Í≤∞, Îí§Ïùò ÎÇòÎ®∏ÏßÄ Ï£ºÏÜåÎ•º Î∂ôÏó¨ÏÑú Ï≤òÎ¶¨ÌïúÎã§.
//ES6 Î¨∏Î≤ï: ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ Î≥ÄÏàòÏôÄ Î¨∏ÏûêÎ•º Ïó∞Í≤∞ÌïòÎäî Í≤ÉÏùÑ ``Î∞±Ìã±Í≥º $Îã¨Îü¨ Ïì∞Î©∞ Î∞îÍøÄ Ïàò ÏûàÎã§.
function fetchNewsList() {
  //   return axios.get(config.baseUrl + "news/1.json");
  return axios.get(`${config.baseUrl}news/1.json`);
}

function fetchAskList() {
  //   return axios.get(config.baseUrl + "news/1.json");
  return axios.get(`${config.baseUrl}ask/1.json`);
}

function fetchJobsList() {
  //   return axios.get(config.baseUrl + "news/1.json");
  return axios.get(`${config.baseUrl}jobs/1.json`);
}

function fetchUserInfo(username) {
  return axios.get(`${config.baseUrl}user/${username}.json`);
}

function fetchCommentItem(id) {
  return axios.get(`${config.baseUrl}item/${id}.json`);
}

function fetchList(pageName) {
  return axios.get(`${config.baseUrl}${pageName}/1.json`);
}

export {
  fetchNewsList,
  fetchAskList,
  fetchJobsList,
  fetchUserInfo,
  fetchCommentItem,
  fetchList,
};

